.home-page .popup-container{
    margin:7.5% 0;
    padding: 0 7.5%;
    .click{
        text-align: center;
        h1{
            font-size: 30px;
            margin-bottom: 20px;
        }
        button{
            padding:10px 30px;
            border:1px solid #ddd;
            background: #eee;
            font-size: 17px;
            border-radius: 5px;
            cursor: pointer;
        }
    }
}
.home-page .popup-card{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    border:1px solid #ddd;
    border-radius: 10px;
    width: 50%;
    padding:25px;
    z-index: 10000;
    .title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        h6{
            font-size: 18px;
            margin:5px;
        }
        span{
            color:#2072ef;
            font-size: 18px;
            margin:5px;
        }
    }
    .colors{
        padding:10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        border-bottom: 1px solid #ddd;
        p{
            font-size: 17px;
            margin:5px 20px 5px 0;
        }
    }
    .color{
        display: flex;
        align-items: center;
        font-size: 16px;
    }
    .col1{
        background: #444;
        width: 45px;
        height: 18px;
        border-radius: 3px;
        margin-left: 10px;
    }
    .col2{
        background: #fff;
        width: 45px;
        height: 18px;
        border-radius: 3px;  
        margin-left: 10px;
        border: 1px solid #eee;
    }
    .col3{
        background: #2072ef;
        width: 45px;
        height: 18px;
        border-radius: 3px;
        margin-left: 10px;
    }
    .col4{
        background: #2072ef;
        width: 45px;
        height: 18px;
        border-radius: 3px;
        margin-left: 10px;
    }
    .col5{
        background: #0053d1;
        width: 45px;
        height: 18px;
        border-radius: 3px;
        margin-left: 10px;
    }
    .buttons{
        display: flex;
        justify-content: flex-end;
        margin-top: 25px;
    }
    .buttons button{
        font-size: 17px;
        padding:7px 15px;
        border-radius: 5px;
        cursor: pointer;
    }
    .button1{
        border:1px solid #ddd;
        background: #eee;
        margin-right: 15px;
    }
    .button2{
        border:1px solid #2072ef;
        background: #2072ef;
        color:#fff;
    }
}
.home-page .popup-backdrop{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(0,0,0,0.75), rgba(0,0,0,0.75));
    z-index: 9000;
}

@media(max-width:1000px){
    .home-page .popup-container{
        .click{
            h1{
                font-size: 20px;
            }
            button{
                font-size: 15px;
            }
        }
    }
    .home-page .popup-card{
        padding:20px 15px;
        width: 300px;
        max-width: 90%;
        height: 65vh;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 7.5px;
        }
        &::-webkit-scrollbar-track {
            background: #ffeded;
            border-radius: 5px;
        }
        &::-webkit-scrollbar-thumb {
            background-color:  #ffaeb1;
            border-radius: 5px;
        }
        
        .title{
            h6{
                font-size: 15px;
            }
            span{
                font-size: 15px;
            }
        }
        .colors{
            p{
                font-size: 15px;
                margin:5px 10px 5px 0;
            }
        }
        .color{
            font-size: 15px;
        }
        .col1{
            width: 40px;
            margin-left: 7.5px;
        }
        .col2{
            width: 40px;
            margin-left: 7.5px;
        }
        
        .col3 {
            width: 40px ;
            margin-left: 7.5px;
        }
        .col4 {
            width: 40px ;
            margin-left: 7.5px;
        }
        .col5 {
            width: 40px ;
            margin-left: 7.5px;
        }
        .buttons button{
            font-size: 15px;
        }
        .button1{
            margin-right: 10px;
        }
        p.p1 {
            width: 100px;
        }
    }
}
